@layer components {
	.hero-title {
		@apply font-bold text-6xl text-pink-600 text-center mb-5;
	}
	.tab {
		@apply text-2xl;
	}
	h1 {
		@apply text-2xl font-bold text-center mb-5;
	}

	/* others.. */
	.btn-primary {
		@apply bg-blue-500 hover:bg-blue-700 text-white font-bold py-2 px-4 rounded-full;
	}
	.btn-secondary {
		@apply bg-white hover:bg-blue-500 hover:text-white hover:border-transparent border border-blue-500 text-blue-700 font-bold py-2 px-4 rounded-full;
	}

	.field-container {
		@apply my-2 flex justify-between;
	}
	.field {
		@apply text-gray-700 text-lg mx-2 font-bold;
	}
	.field-input {
		@apply border border-gray-200 focus:outline-gray-400 p-1 rounded-md text-gray-600 text-center w-[+250px];
		/* to remove focus outline use: focus:outline-none */
	}

	.underline-page-tab {
		@apply underline decoration-[+3px] decoration-pink-400 font-bold;
	}
	.btn-contact {
		@apply btn-primary mr-2 mt-2;
	}

	.box-shadow {
		@apply bg-white shadow-lg rounded pt-6 mt-5 pb-8 px-8;
	}
	.card-center {
		@apply flex-col max-w-lg m-auto box-shadow;
	}
}
